import React, { useEffect, useRef, useState } from "react";
import { connect } from "react-redux";
import Row from "./units/Row";

function Comparison({ comparison }) {
  const [SectionTopOffset, setSectionTopOffset] = useState(0);
  const [offset, setOffset] = useState(0);
  const SectionTop = useRef();
  useEffect(() => {
    const onScroll = () => {
      setOffset(window.pageYOffset);
      setTimeout(() => {
        setSectionTopOffset(SectionTop.current.offsetTop);
        // console.log(SectionTop.current.offsetTop, SectionTop);
        if (window.innerWidth < 768) {
          // setSectionTopOffset(SectionTopOffset + 200);
        }
        if (window.innerWidth > 768 && window.innerWidth < 992) {
          // setSectionTopOffset(SectionTopOffset + 100);
        }
        if (window.innerWidth > 992) {
          // setSectionTopOffset(SectionTopOffset + 500);
        }
      }, 0);
    };
    // clean up code
    window.removeEventListener("scroll", onScroll);
    window.addEventListener("scroll", onScroll, { passive: true });
    return () => window.removeEventListener("scroll", onScroll);
  }, []);
  return (
    <section className="comparison">
      <div className="container">
        <div
          ref={SectionTop}
          className={`comparison-top ${
            offset > SectionTopOffset - 290 ? "show-section-top" : ""
          }`}
        >
          <h2 className="comparison-header">{comparison.header}</h2>
          <p className="comparison-paragraph">{comparison.paragraph}</p>
        </div>
        <div className="comparison-bottom">
          <div className="table-headers">
            <div className="t-head first-header">
              {comparison.columns.firstHeader}
            </div>
            <div className="t-head sec-header">
              {comparison.columns.secHeader}
            </div>
          </div>
          <div className="table-rows">
            {comparison.rows.map((el, inx) => (
              <Row
                key={inx + el.id}
                className={`row${inx + 1}`}
                header={el.header}
                first={el.first}
                sec={el.sec}
                firstHeader={comparison.columns.firstHeader}
                secHeader={comparison.columns.secHeader}
              />
            ))}
          </div>
        </div>
      </div>
    </section>
  );
}

const mapStateToProps = (state) => {
  return {
    comparison: state.dataText.dataJson.home.comparison,
  };
};

export default connect(mapStateToProps)(Comparison);
