import React from "react";

function Row({ className, header, first, sec, firstHeader, secHeader }) {
  return (
    <div className={`row ${className}`}>
      <div className="row-header">{header}</div>
      <div className="row-cell row-first">
        <div className="name-column-mobile">{firstHeader}</div>
        {first === "true" ? (
          <i className="fa-solid fa-circle-check fa-2x"></i>
        ) : (
          <i className="fa-solid fa-circle-xmark fa-2x"></i>
        )}
      </div>
      <div className="row-cell row-sec">
        <div className="name-column-mobile">{secHeader}</div>
        {sec === "true" ? (
          <i className="fa-solid fa-circle-check fa-2x"></i>
        ) : (
          <i className="fa-solid fa-circle-xmark fa-2x"></i>
        )}
      </div>
    </div>
  );
}

export default Row;
