import React, { useEffect, useRef, useState } from "react";
import { connect } from "react-redux";
import Step from "./units/Step";

function HowWork({ howWork, howWorkImgs }) {
  const [howWorkTopOffset, setHowWorkTopOffset] = useState(0);
  const [offset, setOffset] = useState(0);
  const howWorkTop = useRef();
  useEffect(() => {
    const onScroll = () => {
      setOffset(window.pageYOffset);
      setTimeout(() => {
        setHowWorkTopOffset(howWorkTop.current.offsetTop);
        // console.log(howWorkTop.current.offsetHeight);
        if (window.innerWidth < 768) {
          setHowWorkTopOffset((pre) => pre + 200);
        }
        if (window.innerWidth > 768 && window.innerWidth < 992) {
          setHowWorkTopOffset((pre) => pre + 330);
        }
        if (window.innerWidth > 992) {
          setHowWorkTopOffset((pre) => pre + 330);
        }
      }, 0);
    };
    // clean up code
    window.removeEventListener("scroll", onScroll);
    window.addEventListener("scroll", onScroll, { passive: true });
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  return (
    <section className="how-work">
      <div className="container">
        <div
          ref={howWorkTop}
          className={`how-work-top ${
            offset > howWorkTopOffset ? "show-section-top" : ""
          }`}
        >
          <h2 className="how-work-header">{howWork.header}</h2>
          <p className="how-work-paragraph">{howWork.paragraph}</p>
        </div>
        <div className="steps-container">
          {howWork.steps.map((el, inx) => (
            <Step
              key={inx + el.id}
              className={`step${inx + 1}`}
              image={howWorkImgs.howWork}
              imageInside={howWorkImgs[`step${inx + 1}`]}
              header={el.header}
              paragraph={el.paragraph}
              offset={offset}
            />
          ))}
        </div>
      </div>
    </section>
  );
}

const mapStateToProps = (state) => {
  return {
    howWork: state.dataText.dataJson.home.howWork,
    howWorkImgs: state.allImages.pagesImgs.homeImgs.howWorkImgs,
  };
};

export default connect(mapStateToProps)(HowWork);
