import React from "react";
import { useState } from "react";
import { connect } from "react-redux";
import PricingItem from "./units/PricingItem";

function PricingLists({ pricing, lang }) {
  const [valueRange, setValueRange] = useState(100);

  const styleBg = `${
    ((valueRange - pricing.range[0]) /
      (pricing.range[pricing.range.length - 1] - pricing.range[0])) *
      100 +
    "%"
  }`;

  return (
    <div className="pricing-block-box">
      {/* <div className="pricing-block-top">
        <div className="range-wrapped">
          <div
            className="above-range"
            style={
              lang === "en"
                ? { left: `calc(${styleBg} + 20px)` }
                : { right: `calc(${styleBg} + 20px)` }
            }
          >
            <div className="range-before"></div>
            <div className="range-after">
              <span>{valueRange}</span>
            </div>
          </div>
          <input
            type="range"
            name="priceRange"
            id="priceRange"
            className="priceRange"
            min={pricing.range[0]}
            max={pricing.range[pricing.range.length - 1]}
            value={valueRange}
            onChange={(e) => setValueRange(e.currentTarget.value)}
            data-value={valueRange}
            style={{ backgroundSize: styleBg }}
          />
        </div>

        <div className="range-nums">
          {pricing.range.map((el, inx) => (
            <div
              key={"range" + inx}
              className={"range range" + (inx + 1)}
              data-num={el}
            ></div>
          ))}
        </div>
        <div className="range-nums-text">
          {pricing.range.map((el, inx) => (
            <div
              key={"rangeNums" + inx}
              className={"range-text range-text" + (inx + 1)}
            >
              {el}
            </div>
          ))}
        </div>
      </div> */}
      <div className="pricing-block-bottom">
        {pricing.items.map((el, inx) => (
          <PricingItem
            key={inx + el.id}
            className={el.id}
            header={el.header}
            advantage={el.advantage}
            isActive={el.isActive}
            priceDefault={valueRange ? parseInt(valueRange) + 200 : el.priceDefault}
            time={pricing.time}
            btnChoose={pricing.btnChoose}
            btnContact={pricing.btnContact}
            discount={pricing.discount}
            coins={pricing.coins}
          />
        ))}
      </div>
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    pricing: state.dataText.dataJson.home.pricing,
    lang: state.langDir.lang,
  };
};

export default connect(mapStateToProps)(PricingLists);
