import React, { useRef, useState } from "react";
import { useEffect } from "react";

function Step({ className, image, imageInside, header, paragraph, offset }) {
  const [stepItemOffset, setStepItemOffset] = useState(0);
  const stepItem = useRef();
  useEffect(() => {
    const onScroll = () => {
      setStepItemOffset(stepItem ? stepItem.current.offsetTop : 0);
      // console.log(stepItem.current.offsetHeight, stepItem);
      if (window.innerWidth < 768) {
          // setStepItemOffset((pre) => pre - 500);
      }
      if (window.innerWidth > 768 && window.innerWidth < 992) {
          // setStepItemOffset((pre) => pre - 500);
      }
      if (window.innerWidth > 992) {
          // setStepItemOffset((pre) => pre - 500);
      }
    };
    // clean up code
    window.removeEventListener("scroll", onScroll);
    window.addEventListener("scroll", onScroll, { passive: true });
    return () => window.removeEventListener("scroll", onScroll);
  }, []);
  return (
    <div
      className={`Step-item ${className} ${
        offset > stepItemOffset + 550 ? "show" : ""
      }`}
      ref={stepItem}
    >
      <div className="hand-img">
        <img src={image} alt={header} />
        <div className="image-in">
          <img src={imageInside} alt="" />
        </div>
      </div>
      <h4>{header}</h4>
      <p>{paragraph}</p>
    </div>
  );
}

export default Step;
