// libraries
import React, { lazy, Suspense } from "react";
import { Routes, Route, Navigate } from "react-router-dom";
// component
import Home from "./../component/UI/pages/home/Home";

// const LazyHome = lazy(() => {
//   return Promise.all([
//     import("./../component/UI/pages/home/Home"),
//     new Promise((resolve) => setTimeout(resolve, 0)),
//   ]).then(([moduleExports]) => {
//     document.body.style.overflow = "auto";
//     return moduleExports;
//   })
// });

const LazyLogin = lazy(() => {
  return Promise.all([
    import("./../component/UI/pages/login/Login"),
    new Promise((resolve) => setTimeout(resolve, 0)),
  ]).then(([moduleExports]) => {
    document.body.style.overflow = "auto";
    return moduleExports;
  });
});
const LazySignUp = lazy(() => {
  return Promise.all([
    import("./../component/UI/pages/signup/SignUp"),
    new Promise((resolve) => setTimeout(resolve, 0)),
  ]).then(([moduleExports]) => {
    document.body.style.overflow = "auto";
    return moduleExports;
  });
});
const LazyPricing = lazy(() => {
  return Promise.all([
    import("./../component/UI/pages/pricing/Pricing"),
    new Promise((resolve) => setTimeout(resolve, 0)),
  ]).then(([moduleExports]) => {
    document.body.style.overflow = "auto";
    return moduleExports;
  });
});
const LazyContactUs = lazy(() => {
  return Promise.all([
    import("./../component/UI/pages/contactUs/ContactUs"),
    new Promise((resolve) => setTimeout(resolve, 0)),
  ]).then(([moduleExports]) => {
    document.body.style.overflow = "auto";
    return moduleExports;
  });
});
const LazyAboutUs = lazy(() => {
  return Promise.all([
    import("./../component/UI/pages/aboutUs/AboutUs"),
    new Promise((resolve) => setTimeout(resolve, 0)),
  ]).then(([moduleExports]) => {
    document.body.style.overflow = "auto";
    return moduleExports;
  });
});
const LazyNotFound = lazy(() => {
  return Promise.all([
    import("./../component/UI/pages/notFound/NotFound"),
    new Promise((resolve) => setTimeout(resolve, 0)),
  ]).then(([moduleExports]) => {
    document.body.style.overflow = "auto";
    return moduleExports;
  });
});
function Router() {
  return (
    <Routes>
      {/* <Route
        path="home"
        element={
          <Suspense fallback={<div>Loading</div>}>
            <LazyHome />
          </Suspense>
        }
      /> */}
      <Route path="/home" element={<Home />} />
      <Route path="/" element={<Navigate to="/Home" />} />
      <Route
        path="pricing"
        element={
          <Suspense fallback={<div>Loading</div>}>
            <LazyPricing />
          </Suspense>
        }
      />
      <Route
        path="contactus"
        element={
          <Suspense fallback={<div>Loading</div>}>
            <LazyContactUs />
          </Suspense>
        }
      />
      <Route
        path="aboutus"
        element={
          <Suspense fallback={<div>Loading</div>}>
            <LazyAboutUs />
          </Suspense>
        }
      />
      <Route
        path="login"
        element={
          <Suspense fallback={<div>Loading</div>}>
            <LazyLogin />
          </Suspense>
        }
      />
      <Route
        path="signup"
        element={
          <Suspense fallback={<div>Loading</div>}>
            <LazySignUp />
          </Suspense>
        }
      />
      <Route
        path="*"
        element={
          <Suspense fallback={<div>Loading</div>}>
            <LazyNotFound />
          </Suspense>
        }
      />
    </Routes>
  );
}

export default Router;
