import React, { useEffect, useRef, useState } from "react";
import PricingLists from "./blocks/PricingLists";
import { connect } from "react-redux";

function PricingSection({ pricing }) {
  const [SectionTopOffset, setSectionTopOffset] = useState(0);
  const [offset, setOffset] = useState(0);
  const SectionTop = useRef();
  useEffect(() => {
    const onScroll = () => {
      setOffset(window.pageYOffset);
      setTimeout(() => {
        setSectionTopOffset(SectionTop.current.offsetTop);
        // console.log(SectionTop.current.offsetTop, SectionTop);
        if (window.innerWidth < 768) {
          // setSectionTopOffset(SectionTopOffset + 200);
        }
        if (window.innerWidth > 768 && window.innerWidth < 992) {
          // setSectionTopOffset(SectionTopOffset + 100);
        }
        if (window.innerWidth > 992) {
          // setSectionTopOffset(SectionTopOffset);
        }
      }, 0);
    };
    // clean up code
    window.removeEventListener("scroll", onScroll);
    window.addEventListener("scroll", onScroll, { passive: true });
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  return (
    <section className="pricing">
      <div className="container">
        <div
          ref={SectionTop}
          className={`pricing-top ${
            offset > SectionTopOffset - 500 ? "show-section-top" : ""
          }`}
        >
          <h2 className="pricing-header">{pricing.header}</h2>
          <p className="pricing-paragraph">{pricing.paragraph}</p>
        </div>
        <div className="pricing-block">
          <PricingLists />
        </div>
      </div>
    </section>
  );
}

const mapStateToProps = (state) => {
  return {
    pricing: state.dataText.dataJson.home.pricing,
  };
};

export default connect(mapStateToProps)(PricingSection);
