import React, { useEffect, useRef, useState } from "react";

function PricingItem({
  className,
  header,
  advantage,
  isActive,
  time,
  btnChoose,
  btnContact,
  discount,
  priceDefault,
  coins,
}) {
  const [pricingItemOffset, setPricingItemOffset] = useState(0);
  const [offset, setOffset] = useState(0);
  const pricingItem = useRef();
  useEffect(() => {
    const onScroll = () => {
      setOffset(window.pageYOffset);
      setTimeout(() => {
        setPricingItemOffset(pricingItem.current.offsetTop);
        // console.log(pricingItem.current.offsetTop, pricingItem);
        if (window.innerWidth < 768) {
          // setPricingItemOffset(pricingItemOffset + 200);
        }
        if (window.innerWidth > 768 && window.innerWidth < 992) {
          // setPricingItemOffset(pricingItemOffset + 100);
        }
        if (window.innerWidth > 992) {
          // setPricingItemOffset(pricingItemOffset);
        }
      }, 0);
    };
    // clean up code
    window.removeEventListener("scroll", onScroll);
    window.addEventListener("scroll", onScroll, { passive: true });
    return () => window.removeEventListener("scroll", onScroll);
  }, []);
  return (
    <div
      ref={pricingItem}
      className={`box ${offset > pricingItemOffset - 400 ? "show" : ""}`}
    >
      <div
        className={`pricing-item face front ${className} ${
          isActive === "true" && "active"
        }`}
      >
        <div className="pricing-item-top">
          <h4 className="pricing-item-header">{header}</h4>
          {isActive === "true" && (
            <span className="discount">
              <span data-content={discount + " "}>{discount}</span>
            </span>
          )}
        </div>
        <div className="pricing-item-advantage">
          {advantage.map((el, inx) => (
            <div key={inx + el.id}>
              {el.statues === "true" ? (
                <i className="fa-solid fa-check"></i>
              ) : el.statues === "truePro" ? (
                <i className="fa-solid fa-circle-check"></i>
              ) : el.statues === "false" ? (
                <i className="fa-solid fa-xmark"></i>
              ) : (
                ""
              )}

              <span>{el.text}</span>
            </div>
          ))}
        </div>
        <div className="pricing-item-price">
          <span className="dolar">{coins}</span>
          <span className="price-default">{priceDefault} </span>
          <span className="time">/{time}</span>
        </div>
        <button className="pricing-item-btn">{btnChoose}</button>
      </div>
      <div className="face back"></div>
    </div>
  );
}

export default PricingItem;
